.event-body {
  padding-left: 10%;
  background-color: #ffffff;
  font-family: 'Roboto';
  height: 100%;
  padding-bottom: 2vw;
  min-height: 700px; }

.event-time {
  font-size: 1.3vw;
  color: #868686; }

.event-title {
  font-size: 1.8vw;
  margin: 0px !important;
  color: #5f5f5f; }

.event-description {
  font-size: 1vw;
  color: #868686; }

.schedule-title {
  margin-bottom: 6vw;
  font-size: 5vw;
  font-weight: 900;
  border-bottom: 0.2vw solid #ff5222;
  display: inline-block;
  position: relative; }

.landing-page-table {
  font-family: arial, sans-serif;
  width: 100%;
  border-collapse: collapse; }

.landing-page-th {
  padding: 10px;
  text-align: left;
  padding: 16px;
  border: 0.5px solid #dddddd;
  min-width: 160px; }

landing-page-tr:nth-child(even) {
  background-color: #f7f7f7; }

body:not(.nohover) .landing-page-tbody .landing-page-tr:hover {
  background-color: #dddddd; }

.table-title {
  font-size: 1.5vw;
  color: #000000; }

.landing-page-thead {
  -webkit-box-shadow: 0px 2px 1px #b6b5b5;
          box-shadow: 0px 2px 1px #b6b5b5; }

.schedule-content {
  width: 90%; }

.no-data {
  text-align: center;
  padding-top: 10px;
  font-size: 2vw; }



