landingpage-body {
  font-family: Calibri, Arial, sans-serif;
  background: #FFFFFF; }

.container {
  position: relative;
  margin-top: 5em;
  width: inherit; }

.cbp-af-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: #FFFFFF;
  z-index: 10000;
  height: 80px;
  overflow: hidden;
  -webkit-transition: height 0.3s;
  -o-transition: height 0.3s;
  transition: height 0.3s;
  -webkit-box-shadow: 0px 1px 6px #aaa;
          box-shadow: 0px 1px 6px #aaa; }

.cbp-af-header .cbp-af-inner {
  width: 90%;
  max-width: 69em;
  margin: 0 auto;
  padding: 0 1.875em;
  min-width: 600px; }

/* We just have one-lined elements, so we'll center the elements with the line-height set to the height of the header */
.cbp-af-header h2,
.cbp-af-header nav a {
  line-height: 80px; }
  .cbp-af-header h2.home,
  .cbp-af-header nav a.home {
    color: #ff5722 !important; }

.cbp-af-header h2 {
  text-transform: uppercase;
  color: #ff5722;
  letter-spacing: 4px;
  font-size: 2em;
  margin: 0;
  float: left; }

.cbp-af-header nav {
  float: right; }

.cbp-af-header nav a {
  color: #aaa;
  font-weight: 700;
  margin: 0 0 0 20px;
  font-size: 1em; }

.cbp-af-header nav a:hover {
  color: #333; }

/* Transitions and class for reduced height */
.cbp-af-header h2,
.cbp-af-header.cbp-af-header-shrink {
  height: 90px; }

.cbp-af-header.cbp-af-header-shrink h2,
.cbp-af-header.cbp-af-header-shrink nav a {
  line-height: 90px; }

.cbp-af-header.cbp-af-header-shrink h2 {
  font-size: 2em; }

.btn.get-ticket {
  color: #ff5722;
  margin-left: 20px;
  border: 1.5px solid #ff5722;
  border-radius: 5px !important;
  background: #FFFFFF;
  font-size: 1em;
  cursor: pointer;
  font-weight: 500;
  text-align: center;
  padding: .5em 1.2em .4em 1.2em; }

.row {
  margin: 0px !important; }

.landingpage-title {
  font-size: 2.5vw;
  padding-top: 1.5em; }
  .landingpage-title.description {
    color: #ff5722; }



